window.CP_DATA = {
    certificates: [],
};
const tpl_certificatesList = document.getElementById('tpl_certificates'),
    out_certificatesList = document.getElementById('out_certificates');

function compileHadleBars(tpl, out, data) {
    var _templateScript = tpl.innerHTML
    var _template = Handlebars.compile(_templateScript);
    var _compiledHtml = _template(data);
    out.innerHTML = _compiledHtml;
};
cadesplugin.then(function () {}, function (error) {

});
window.CryptoPro.call('getCertsList').then(function (certList) {
    window.CP_DATA.certificates = certList;
    compileHadleBars(tpl_certificatesList, out_certificatesList, certList);
});
console.log(window.CP_DATA);

document.getElementById('button').addEventListener('click', function () {
    const files = document.getElementById('file').files;
    if (files.length > 0) {
        const file = files[0];
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = function () {
            document.getElementById('base64result').value = reader.result;
        };
        reader.onerror = function (error) {
            console.log('Error: ', error);
        };
    }
});

document.getElementById('generateSign').addEventListener('click', function () {
    const b64Data = document.getElementById('inputb64').value,
          certData = document.getElementById('inputbCertHash').value,
          dataInput = document.getElementById('signResult');
    window.CryptoPro.call('signData', certData, b64Data).then(function (signature) {
        dataInput.value = signature;
    });
});




